import React from "react";
import {Box, Chip, Container, Divider} from "@mui/material";
import {HeroCounterPackGrid, HeroCounterHeroGrid, HeroGrid, HeroPackSelected} from "./index";

interface IProps {
    heroIds: number[]
}

interface IState {
    heroIds: number[],
    heroProbs?: Map<number, number>,
    heroCounterProbs?: Map<number, number>,
    heroLostPacks: number[][],
    heroCounterFilter: number[],
    requestsInProgress: number
}

export class HeroSelect extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            heroIds: props.heroIds ? props.heroIds : [],
            heroLostPacks: [],
            heroCounterFilter: [],
            requestsInProgress: 0
        };
        this.selectHero = this.selectHero.bind(this)
        this.deselectHero = this.deselectHero.bind(this)
        this.filterCounterHero = this.filterCounterHero.bind(this)
    }
    initialized: boolean = false


    getPacksData(heroIds: number[]) {

        const heroParams=[-1,-1,-1,-1,-1]
        for (let i=0;i<5 && i<heroIds.length;i++) {heroParams[i]=heroIds[i];}
        const state=this.state; this.setState({requestsInProgress:state.requestsInProgress+1})
        fetch("https://g92e0914be8be53-db40vlv.adb.eu-frankfurt-1.oraclecloudapps.com/ords/hwstat/hws/findLostPackHeroes/"+heroParams.join("/"), {
            method: "GET"
        }).then(res => res.json())
            .then(
                (result) => {

                    if (result['items']) {
                        const heroLostPacks = []
                        let cnt = 0
                        const heroProbs=new Map<number,number>()
                        for (const x in result['items']) {
                            const z=result['items'][x]
                            const lostPack = [~~z.a1,~~z.a2,~~z.a3,~~z.a4,~~z.a5]
                            for (const heroId of lostPack) {
                                    let m = heroProbs.get(~~heroId)
                                    if (m) {
                                        m++
                                    } else {
                                        m=1
                                    }
                                    heroProbs.set(~~heroId,m)

                             }
                            cnt++
                            lostPack.push(Number.parseFloat(z.r))
                            heroLostPacks.push(lostPack)
                        }
                        for (const heroPackId of Array.from(heroProbs.keys())) {
                            const mmd=heroProbs.get(~~heroPackId)
                            if (mmd && cnt >0) heroProbs.set(~~heroPackId,100*mmd/cnt)
                        }
                        this.setState(
                            {heroIds: heroIds, heroCounterProbs: heroProbs, heroLostPacks: heroLostPacks}
                        )
                    }
                }).finally(()=>{
                    const state=this.state; this.setState({requestsInProgress:state.requestsInProgress-1})
        })
    }

    updateHeroIds(heroIds: number[]) {
        this.initialized=true
        this.setState(
            {heroIds: heroIds, heroProbs: undefined, heroLostPacks: []}
        )
        if (heroIds.length == 5) {
            this.getPacksData(heroIds)
        } else {
            const heroParams=[-1,-1,-1,-1,-1]
            for (let i=0;i<5 && i<heroIds.length;i++) {heroParams[i]=heroIds[i];}
            const state=this.state; this.setState({requestsInProgress:state.requestsInProgress+1})
            fetch("https://g92e0914be8be53-db40vlv.adb.eu-frankfurt-1.oraclecloudapps.com/ords/hwstat/hws/findLostPackHeroesStat/"+heroParams.join("/"), {
                method: "GET"
            })
                .then(res => res.json())
                .then(
                    (result) => {
                        let heroProbs = new Map<number, number>()
                        if (result['items']) {
                            for (const j in result['items']) {
                                if (result['items'][j]['d']) {
                                    heroProbs.set(~~result['items'][j]['d'], Number.parseFloat(result['items'][j]['prob']))
                                }
                            }
                        }
                        this.setState({
                            heroProbs: heroProbs,
                            heroIds: heroIds,
                            heroLostPacks: []
                        });
                    },
                    // Примечание: важно обрабатывать ошибки именно здесь, а не в блоке catch(),
                    // чтобы не перехватывать исключения из ошибок в самих компонентах.
                    (error) => {
                        let heroProbs = new Map<number, number>()
                        this.setState({
                            heroProbs: undefined,
                            heroIds: heroIds,
                            heroLostPacks: []
                        });
                    }
                ).finally(()=>{
                const state=this.state; this.setState({requestsInProgress:state.requestsInProgress-1})
            })
        }
    }

    updateHeroCounterFilter(heroCounterFilter: number[]) {
        this.setState({heroCounterFilter: heroCounterFilter})
    }

    filterCounterHero(e: any) {
       // console.log('HF0',e.target)
        if (e && e.target && e.target.dataset && e.target.dataset.heroid) {
            const heroId = ~~e.target.dataset.heroid
            const heroCounterFilter = this.state.heroCounterFilter
            const index = heroCounterFilter.indexOf(heroId, 0)
            if (index > -1) {
                heroCounterFilter.splice(index, 1);
                this.updateHeroCounterFilter(heroCounterFilter)
            } else {
                heroCounterFilter.push(heroId)
                this.updateHeroCounterFilter(heroCounterFilter)
            }
        }
    }

    selectHero(e: any) {
        if (e && e.target && e.target.dataset && e.target.dataset.heroid) {
            const heroId = ~~e.target.dataset.heroid
            const heroIds = this.state.heroIds
            if (!heroIds.includes(heroId) && heroIds.length < 5) {
                heroIds.push(heroId)
                this.updateHeroIds(heroIds)
            }
        }
    }

    deselectHero(e: any) {
        if (e && e.target && e.target.dataset && e.target.dataset.heroid) {
            const heroId = ~~e.target.dataset.heroid
            const heroIds = this.state.heroIds
            const index = heroIds.indexOf(heroId, 0)
            if (index > -1) {
                heroIds.splice(index, 1);
                this.updateHeroIds(heroIds)
            }
        }
    }

    render() {

            if (this.state.heroIds.length == 5 && this.state.heroLostPacks.length > 0) {
                if (!this.initialized) {
                    this.updateHeroIds(this.state.heroIds)
                }
                return (
                    <Container sx={{
                        bgcolor: 'background.default'
                    }}>
                        <Divider><Chip label={this.state.requestsInProgress === 0?"Selected pack":"Loading...just wait!"}/></Divider>
                        <HeroPackSelected heroIds={this.state.heroIds} deselectHero={this.deselectHero}/>
                        <Divider><Chip label={this.state.requestsInProgress === 0?"Counter heroes - fill free to filter packs":"Loading...just wait!"}/></Divider>
                        <HeroCounterHeroGrid heroProbs={this.state.heroCounterProbs}
                                             onFilterCounterHero={this.filterCounterHero}
                                             heroCounterFilter={this.state.heroCounterFilter}/>
                        <Divider><Chip label={this.state.requestsInProgress === 0?"Counter hero packs":"Loading...just wait!"}/></Divider>
                        <HeroCounterPackGrid heroLostPacks={this.state.heroLostPacks}
                                             onFilterCounterHero={this.filterCounterHero}
                                             heroCounterFilter={this.state.heroCounterFilter}/>
                    </Container>
                )
            } else {
                if (!this.initialized) {
                    this.updateHeroIds(this.state.heroIds)
                }
                return (
                    <Container sx={{
                        //width: '100%',
                        m: 0.25,
                        p: 0.25,
                        bgcolor: 'background.default'
                    }}>
                        <HeroPackSelected heroIds={this.state.heroIds} deselectHero={this.deselectHero}/>
                        <Divider><Chip label={this.state.requestsInProgress === 0?"Choose pack to attack":"Loading...just wait!"}/></Divider>
                        <Box sx={{
                            m: 0,
                            p: 0
                        }}>
                            <HeroGrid heroIds={this.state.heroIds} heroProbs={this.state.heroProbs}
                                      selectHero={this.selectHero}/>
                        </Box>
                    </Container>
                )
            }

    }
}

export default HeroSelect;